var util = require('../../../common/util.js');
export default {
	data() {
		return {
			collect: false,
			subject: '',
			type: 0,
			current: 0,
			listData: [],
			maxTime: 2700,
			timer: '',
			open: false,
			answerData: [],
			questionId: '',
			yes: 0,
			wrong: 0,
			disabled: false
		}
	},
	onLoad(options) {
		uni.showToast({
			icon: 'loading',
			title: '数据加载中...',
			duration: 1000
		});
		clearInterval(this.timer)
		this.subject = options.subject
		this.openCountDown()
		// 获取题目
		uni.request({
			url: this.$Url + '/api/v1/exam/item/' + options.subject,
			method: 'GET',
			data: {
				token: uni.getStorageSync('token')
			},
			header: {
				'content-type': 'application/x-www-form-urlencoded'
			},
			success: (res) => {
				if (res.data.code == 200) {
					let arr = []
					for (let i in res.data.msg) {
						if (res.data.msg[i].url != '') {
							res.data.msg[i].url =
								'<div><img style="display: block;margin: 0 auto;max-width: 80%;max-height: 150px;" src="' + res.data.msg[i]
								.url + '"/></div>'
						}
						arr.push(res.data.msg[i])
					}
					this.listData = res.data.msg
					this.questionId = res.data.msg[0].questionId
					this.checkCollect()
					let answerData = []
					for (let i = 0; i < res.data.msg.length; i++) {
						let obj = new Object()
						obj.questionId = res.data.msg[i].questionId;
						obj.disabled = false;
						obj.answer = '';
						obj.result = 0
						answerData.push(obj)
					}
					this.answerData = answerData
					this.answerData[0].result = 3
				} else {
					uni.showToast({
						icon: 'none',
						title: '网络不给力，请稍后重试',
						duration: 2000
					});
				}
			}
		});
	},
	onHide() {
		clearInterval()
	},
	onBackPress() {
		clearInterval(this.timer)
	},
	onNavigationBarButtonTap: function(e) {
		console.log(e);
	},
	methods: {
		checkCollect: function(e) {
			uni.request({
				url: this.$Url + '/api/v1/exam/collection/check',
				method: 'POST',
				data: {
					'token': uni.getStorageSync('token'),
					'questionId': this.questionId
				},
				header: {
					'content-type': 'application/x-www-form-urlencoded'
				},
				success: (res) => {
					if (res.data.code == 200) {
						if (res.data.msg != null) {
							this.collect = true
						} else {
							this.collect = false
						}
					} else {
						uni.showToast({
							icon: 'none',
							title: '网络不给力，请稍后重试',
							duration: 2000
						});
					}
				}
			});
		},
		tapCollect: function(e) {
			this.collect = !this.collect
			if (this.collect) {
				uni.request({
					url: this.$Url + '/api/v1/exam/collection/store',
					method: 'POST',
					data: {
						'token': uni.getStorageSync('token'),
						'questionId': this.questionId
					},
					header: {
						'content-type': 'application/x-www-form-urlencoded'
					},
					success: (res) => {
						if (res.data.code == 200) {
							uni.showToast({
								title: '收藏成功',
								duration: 1000
							});
						} else {
							uni.showToast({
								icon: 'none',
								title: '网络不给力，请稍后重试',
								duration: 2000
							});
						}
					}
				});
			} else {
				uni.request({
					url: this.$Url + '/api/v1/exam/collection/cancel',
					method: 'POST',
					data: {
						'token': uni.getStorageSync('token'),
						'questionId': this.questionId
					},
					header: {
						'content-type': 'application/x-www-form-urlencoded'
					},
					success: (res) => {
						if (res.data.code == 200) {
							uni.showToast({
								title: '取消成功',
								duration: 1000
							});
						} else {
							uni.showToast({
								icon: 'none',
								title: '网络不给力，请稍后重试',
								duration: 2000
							});
						}
					}
				});
			}
		},
		CountDown: function(e) {
			if (this.maxTime >= 0) {
				let M = Math.floor(this.maxTime / 60);
				let S = Math.floor(this.maxTime % 60);
				if (M <= 9) M = '0' + M;
				if (S <= 9) S = '0' + S;
				uni.setNavigationBarTitle({
					title: '倒计时' + M + ':' + S
				});
			} else {
				uni.setNavigationBarTitle({
					title: '考试结束'
				});
				uni.showModal({
					title: '温馨提示',
					content: '考试不通过',
					showCancel: false,
					confirmText: '交卷',
					success: (res) => {
						if (res.confirm) {
							clearInterval()
							uni.navigateBack()
						}
					}
				});
			}
		},
		openCountDown: function() {
			this.timer = setInterval(() => {
				this.maxTime--
				this.CountDown()
			}, 1000)
		},
		DoTitle: function(sign, choose) {
			let subject = this.subject == 'one' ? '1' : '4'
			uni.request({
				url: this.$Url + '/api/v1/exam/mock/log',
				method: 'GET',
				data: {
					token: uni.getStorageSync('token'),
					subject: subject,
					questionId: this.questionId,
					sign: sign,
					answer: this.listData[this.current].answer,
					choose: choose
				},
				header: {
					'content-type': 'application/x-www-form-urlencoded'
				},
				success: (res) => {}
			});
		},
		change: function(e) {
			console.log(this.answerData)
			this.current = e.detail.current
			this.questionId = this.listData[e.detail.current].questionId
			this.disabled = this.answerData[e.detail.current].disabled
			this.checkCollect()
		},
		radioChange: function(e) {
			this.answerData[this.current].answer = e.target.value
			this.disabled = true
			this.answerData[this.current].disabled = true
			if (this.listData[this.current].answer == e.target.value) {
				this.DoTitle('r', parseInt(e.target.value) + 1)
				this.answerData[this.current].result = 1
				this.yes++
				uni.showModal({
					title: '温馨提示',
					content: '恭喜您答对了',
					showCancel: false,
					confirmText: '下一题',
					success: (res) => {
						if (res.confirm) {
							this.current++
							console.log('用户点击确定');
						}
					}
				});
			} else {
				this.DoTitle('w', parseInt(e.target.value) + 1)
				this.answerData[this.current].result = 2
				this.wrong++
				uni.showModal({
					title: '温馨提示',
					content: '很抱歉您答错了',
					showCancel: false,
					confirmText: '下一题',
					success: (res) => {
						if (res.confirm) {
							this.current++
							console.log('用户点击确定');
						}
					}
				});
			}
			this.handPaper()
		},
		checkboxChange: function(e) {
			let str = ''
			switch (e.detail.value.join('')) {
				case 'AB':
					str = "7";
					break;
				case 'AC':
					str = "8";
					break;
				case 'AD':
					str = "9";
					break;
				case 'BC':
					str = "10";
					break;
				case 'BD':
					str = "11";
					break;
				case 'CD':
					str = "12";
					break;
				case 'ABC':
					str = "13";
					break;
				case 'ABD':
					str = "14";
					break;
				case 'ACD':
					str = "15";
					break;
				case 'BCD':
					str = "16";
					break;
				case 'ABCD':
					str = "17";
					break;
			}
			this.answerData[this.current].answer = str
		},
		judgeChange: function(e) {
			this.answerData[this.current].answer = e.target.value
			this.disabled = true
			this.answerData[this.current].disabled = true
			if (this.listData[this.current].answer == e.target.value) {
				this.yes++
				this.DoTitle('r', parseInt(e.target.value) + 1)
				this.answerData[this.current].result = 1
				uni.showModal({
					title: '温馨提示',
					content: '恭喜您答对了',
					showCancel: false,
					confirmText: '下一题',
					success: (res) => {
						if (res.confirm) {
							this.current++
							console.log('用户点击确定');
						}
					}
				});
			} else {
				this.DoTitle('w', parseInt(e.target.value) + 1)
				this.answerData[this.current].result = 2
				this.wrong++
				uni.showModal({
					title: '温馨提示',
					content: '很抱歉您答错了',
					showCancel: false,
					confirmText: '下一题',
					success: (res) => {
						if (res.confirm) {
							this.current++
							console.log('用户点击确定');
						}
					}
				});
			}
			this.handPaper()
		},
		tapAnswer: function(e) {
			this.open = !this.open
		},
		CloseAnswer: function(e) {
			this.open = !this.open
		},
		tapQuestionId: function(e) {
			this.current = e.currentTarget.dataset.idx
			this.open = !this.open
		},
		tapCheckbox: function(e) {
			let index = e.currentTarget.dataset.index
			this.disabled = true
			this.answerData[this.current].disabled = true
			if (this.listData[this.current].answer == this.answerData[index]) {
				this.yes++
				this.DoTitle('r', this.answerData[index])
				this.answerData[this.current].result = 1
				uni.showModal({
					title: '温馨提示',
					content: '恭喜您答对了',
					showCancel: false,
					confirmText: '下一题',
					success: (res) => {
						if (res.confirm) {
							this.current++
							console.log('用户点击确定');
						}
					}
				});
			} else {
				this.DoTitle('w', this.answerData[index])
				this.answerData[this.current].result = 2
				this.wrong++
				uni.showModal({
					title: '温馨提示',
					content: '很抱歉您答错了',
					showCancel: false,
					confirmText: '下一题',
					success: (res) => {
						if (res.confirm) {
							this.current++
							console.log('用户点击确定');
						}
					}
				});
			}
			this.handPaper()
		},
		handPaper: function(e) {
			if (this.subject == 'one' && this.wrong > 10 || this.subject == 'four' && this.wrong > 5) {
				uni.showModal({
					title: '温馨提示',
					content: '考试不通过',
					showCancel: false,
					confirmText: '交卷',
					success: (res) => {
						if (res.confirm) {
							clearInterval()
							uni.navigateBack()
						}
					}
				});
			}
		},
		CloseExam: function(e) {
			uni.showModal({
				title: '温馨提示',
				content: '确定提交试卷？',
				showCancel: true,
				cancelColor: '#3860ff',
				confirmColor: '#929292',
				success: function(res) {
					if (res.confirm) {
						clearInterval()
						uni.navigateBack()
					} else if (res.cancel) {
						console.log('用户点击取消');
					}
				}
			});
		}
	}
}
